
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { Truck } from "lucide-react";
import Image from "next/image";
import { generateBlueprint } from "@/ai/flows/generate-blueprint";

export default async function BlueprintPage() {
  const prompt = `# The Journey to a Planet-Scale Transport Platform

**Era 1: Foundational Operations** (Phases 1-6)
**Era 2: Global Intelligence & Resilience** (Phases 7-12)
**Era 3: True Autonomy & Governance** (Phases 13-15)

Key phases include:
- Establish Real-Time Control
- Enable Data-Driven Insights
- Achieve Production-Grade Reliability
- Harden for Sovereign & Secure Operations
- Achieve Planet-Scale Federation
- Deploy On-Vehicle Edge AI
- Activate Swarm Intelligence (V2V)
- Implement Human-In-the-Loop Governance`;

  let imageUrl = "https://picsum.photos/seed/blueprint-error/1200/675";
  let altText = "Placeholder blueprint image";

  try {
    const result = await generateBlueprint({ prompt });
    imageUrl = result.imageUrl;
    altText = "Architectural blueprint of a planet-scale transport platform";
  } catch (error) {
    console.error("Failed to generate blueprint image:", error);
    // The imageUrl is already set to a placeholder, so the page will still render.
  }


  return (
    <div className="flex flex-col min-h-screen bg-background">
      <header className="container mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex h-20 items-center justify-between">
          <Link href="/" className="flex items-center gap-2 font-bold text-xl">
            <Truck className="h-7 w-7 text-primary" />
            <span>MHB Logistics</span>
          </Link>
          <nav className="flex items-center gap-4">
             <Button variant="outline" asChild>
                <Link href="/">Home</Link>
            </Button>
            <Button asChild>
                <Link href="/login">Login</Link>
            </Button>
          </nav>
        </div>
      </header>
      <main className="flex-1 container mx-auto px-4 sm:px-6 lg:px-8 py-12 flex items-center justify-center">
        <div className="w-full">
            <Image
                src={imageUrl}
                alt={altText}
                width={1200}
                height={675}
                className="rounded-lg shadow-lg"
                data-ai-hint="architecture blueprint"
            />
        </div>
      </main>
       <footer className="py-8 bg-background border-t">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8 text-center text-muted-foreground">
          <p>&copy; {new Date().getFullYear()} MHB Logistics. All rights reserved.</p>
        </div>
      </footer>
    </div>
  );
}
